import { Component, OnInit, Input, Output, EventEmitter, Injector, OnDestroy } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { FormGroup } from "@angular/forms";
import { Subscription } from "rxjs";

import { DiagramModel } from "../../models";
import { DiagramService } from "../../services/diagram.service";

@Component({
	selector: "pie-diagram-partial-config",
	template: `<div *ngIf="dataLoaded | async" class="diagram-box">
  <form [formGroup]="formGroup" fxLayout='column'>
    <div>
      <mat-checkbox (change)="configChanged()" formControlName="Legend" fxFlexFill>نمایش توضیحات</mat-checkbox>
    </div>
    <div>
      <mat-checkbox (change)="configChanged()" formControlName="IsActive" fxFlexFill>وضعیت</mat-checkbox>
    </div>
    <div>
      <mat-checkbox (change)="configChanged()" formControlName="Legend" fxFlexFill>نمایش توضیحات</mat-checkbox>
    </div>
    <div>
      <mat-checkbox (change)="configChanged()" formControlName="IsActive" fxFlexFill>وضعیت</mat-checkbox>
    </div>
  </form>
  <form [formGroup]="donutFormGroup">
    <mat-form-field>
      <input matInput placeholder="عنوان" formControlName="title">
    </mat-form-field>
  </form>
</div>`,
	styles: [`:host{width:100%}.diagram-box{margin:15px}.numeric{font-size:3em;line-height:1.8;font-family:iran-sans-bold,Tahoma}.numeric.title{font-size:1.5em}`]
})
export class PieDiagramPartialConfigComponent implements OnInit, OnDestroy {
	@Output() ConfigChanged = new EventEmitter();
	dataSubscribtion: Subscription;
	dataLoaded = new BehaviorSubject(false);
	_dataLoaded;
	@Input()
	set formGroup(data: FormGroup) {
		if (!data) return;
		this._dataLoaded = data;
		this.dataLoaded.next(true);
	}
	get formGroup() {
		return this._dataLoaded;
	}
	donutFormGroup: FormGroup;
	diagramService: DiagramService;
	constructor(private injector: Injector) {
		this.formGroup = this.injector.get("formGroup");
		this.diagramService = this.injector.get("diagramService");
		this.donutFormGroup = this.formGroup.controls.donut as FormGroup;
	}
	ngOnInit() {
		// this.dataSubscribtion = this.diagramService.getData(this.data.Source.Route)
		//   .subscribe(data => {
		//     debugger;
		//   });
	}
	ngOnDestroy() {
		// this.dataSubscribtion.unsubscribe();
	}
	configChanged() {
		this.ConfigChanged.emit();
	}
}
